import React from 'react';
import SideNav from 'react-simple-sidenav';

import SideNavItems from './sidenav_items';

const Nav = (props)=>{
    //console.log("Side Nav Called!");
    
    return (
        <SideNav
            showNav={props.showNav}
            onHideNav = {props.onHideNav}
            navStyle={{
                background:'#242424',
                maxWidth:'220px'
            }}
        >
            <SideNavItems/>
        </SideNav>
    )
}

export default Nav;